<template>
  <input class="hide_file" ref="upload" type="file" @change="getFile($event)" accept="image/*">
  <div v-loading="loading"
       element-loading-text="请稍等，正在上传中">
    <el-image :src="img.base64" class="img"></el-image>
    <el-select v-model="selected" placeholder="请选择情绪">
      <el-option v-for="(item,index) in options" :key="index" :label="item.label" :value="item.value">
      </el-option>
    </el-select>
    <div class="buttons">
      <button class="button" v-on:click=select>选择照片</button>
      <button class="button" v-on:click="upload">上传数据</button>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: "CollectAvatar",
  data() {
    return {
      img: {
        file: undefined,
        base64: "data:image/svg+xml;base64,"
      },
      selected: undefined,
      options: [
        {label: '愤怒', value: 'Angry'},
        {label: '厌恶', value: 'Disgust'},
        {label: '恐惧', value: 'Fear'},
        {label: '快乐', value: 'Happy'},
        {label: '悲伤', value: 'Sad'},
        {label: '惊讶', value: 'Surprise'},
        {label: '中性', value: 'Neutral'}
      ],
      loading: false
    }
  },
  methods: {
    getFile(e) {
      console.log(e.target.files[0])
      const file = e.target.files[0];
      const reader = new FileReader();
      const temp = this
      reader.onloadend = function () {
        temp.img = {file: e.target.files[0], base64: this.result}
      }
      reader.readAsDataURL(file);
    },
    select() {
      // this.removeButtonFocus(e)
      this.$refs.upload.click();
    },
    upload() {
      // this.removeButtonFocus(e)
      if (this.img.file === undefined) {
        this.$message("请选择照片");
        return
      }
      if (this.selected === undefined) {
        this.$message("请选择当前的情绪");
        return
      }
      this.loading = true
      const formData = new FormData()
      formData.append('avatar', this.img.file)
      let config = {
        headers: {'Content-Type': 'multipart/form-data'}
      }
      const localHost = window.location.hostname
      const thisTemp = this
      axios.post(`http://${localHost}:3001/upload?label=${thisTemp.selected}`, formData, config)
          .then(response => {
            thisTemp.loading = false
            thisTemp.$message(response.data["data"]);
          }).catch(error => {
        thisTemp.loading = false
        thisTemp.$message('上传失败' + error);
        console.log(error)
      })
    },
  }
}

</script>


<style scoped>
.hide_file {
  display: none;
}

.img {
  width: 70%;
  margin: 0 auto;
}

.buttons {
  padding-top: 4%;
}

.button {
  width: 40%;
}
</style>